.header {
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-top: 7em;
    position: relative;
}

.header__title {
    color: var(--background-text-secondary-color);
    font-size: 3.2em;
    font-weight: 300;
    margin: .25em 0 .1em;
}

.header__subtitle {
    font-size: 1.6em;
    font-weight: 200;
    margin: 0 0 4em;
}

@media (max-width: 1200px) {
    .header {
        padding-top: 5em;
    }

    .header__subtitle {
        margin-bottom: 2em;
    }
}

@media (max-width: 736px) {
    .header__title {
        font-size: 2.5em;;
    }
}

@media (max-width: 360px) {
    .header__title {
        font-size: 2em;;
    }

    .header__subtitle {
        font-size: 1.4em;
    }
}